.block-genplan {
  .container {
    padding: 0px 0px 0px 0px;
  }

  // .block-genplan__content-container

  &__content-container {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    min-width: 100%;
    height: 100%;
    transform-origin: center center;
    transition: transform 0.3s;
    will-change: transform;
    transform: translate(0px, 0px) scale(1);
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;

    // Добавляем плавные переходы
    opacity: 0;
    visibility: hidden;
    transition:
      opacity 0.5s ease,
      visibility 0.5s ease,
      transform 0.3s;

    &::-webkit-scrollbar {
      display: none;
    }

    @media (max-width: 1180px) {
      width: 1200px;
    }

    &._active {
      opacity: 1;
      visibility: visible;
      z-index: 3;
    }

    // Первый шаг видим по умолчанию
    &.step1 {
      opacity: 1;
      visibility: visible;
      z-index: 3;
    }
  }

  // .block-genplan__content

  &__content {
    position: relative;
    width: 100%;
    height: 631px;
    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 65px;
      z-index: 5;
      background: linear-gradient(0deg, #000 0%, rgba(0, 0, 0, 0) 100%);
    }
  }

  // .block-genplan__body

  &__body {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    cursor: grab;
    z-index: 2;
    background: #f0f0f0;
    &.dragging {
      cursor: grabbing;
    }
  }

  // .block-genplan__svg

  &__svg {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 4;
  }

  // .block-genplan__path

  &__path {
    &._active {
      &.path1 {
        fill: rgba(205, 117, 85, 0.5);
      }
      &.path-blue {
        fill: rgba(12, 39, 118, 0.65);
      }
    }
  }

  // .block-genplan__tips

  &__tips {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 30;
  }

  // .block-genplan__tippy

  &__tippy {
    position: absolute;
    z-index: 30;
    background-color: #fff;
    color: #0c2776;
    border-radius: 38px;
    padding: 12px 11px;
    font-weight: 500;
    font-size: 17px;
    line-height: 78%;
    letter-spacing: -0.03em;
    transition:
      background-color 0.3s,
      color 0.3s;
    &::before {
      content: "";
      position: absolute;
      bottom: -8px;
      transform: translate(-50%);
      left: 50%;
      width: 0;
      height: 0;
      border-left: 12px solid transparent;
      border-right: 12px solid transparent;
      border-top: 10px solid #fff;
      @media (max-width: 768px) {
        border-left: 15px solid transparent;
        border-right: 15px solid transparent;
      }
    }
    .choice-home & {
      &._active {
        background: #ffb26b;
        &::before {
          content: "";
          border-top: 10px solid #ffb26b;
        }
      }
    }
    &._active {
      color: #fff;
      background: #cd7555;
      &::before {
        content: "";
        border-top: 10px solid #cd7555;
      }
    }
    @media (max-width: 768px) {
      font-size: 22px;
      padding: 15px 13px;
      border-radius: 48px;
    }
  }

  // .block-genplan__popups

  &__popups {
  }

  // .block-genplan__image

  &__image {
    display: flex;
    height: 100%;
    overflow: hidden;
    position: relative;
    width: 100%;
    img {
      position: relative;
      transition: all 0.3s ease;
      z-index: 2;
      height: 100%;
      width: 100%;
      -webkit-user-drag: none;
      object-position: center;
    }
  }

  // .block-genplan__title

  &__title {
    position: absolute;
    z-index: 10;
    top: 50px;
    left: 25px;
    display: flex;
    align-items: center;
    font-family: var(--second-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 123%;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: #fff;
    border-radius: 30px;
    padding: 5px 12px 5px 5px;
    min-height: 50px;
    overflow: hidden;
    gap: 10px;

    // Добавляем плавные переходы
    opacity: 0;
    visibility: hidden;
    transition:
      opacity 0.5s ease,
      visibility 0.5s ease;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.65);
      backdrop-filter: blur(12.445961952209473px);
    }

    span {
      position: relative;
      z-index: 5;
    }

    &.title-genplan {
      // Первый заголовок видим по умолчанию
      opacity: 1;
      visibility: visible;
      &.hidden {
        opacity: 0;
        pointer-events: none;
      }
    }

    &.title-house {
      display: flex; // Всегда display: flex, но скрыт через opacity

      svg {
        transition: fill 0.3s;
        transform: rotate(-180deg);
      }

      &:hover {
        .block-genplan__icon {
          background-color: #0c2776;
        }
        svg {
          fill: #fff;
        }
      }
    }

    &._active {
      opacity: 1;
      visibility: visible;
    }

    @media (max-width: 768px) {
      top: 30px;
      left: 15px;
    }
  }

  // .block-genplan__icon

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 5;
    border-radius: 50%;
    background-color: #fff;
    width: 40px;
    height: 40px;
    flex: 0 0 40px;
    transition: background-color 0.3s;
    svg {
      fill: var(--theme-color-accent);
    }
  }

  // .block-genplan__compass

  &__compass {
    width: 94px;
    height: 94px;
    img {
      width: 100%;
      height: 100%;
    }
    @media (max-width: 768px) {
      width: 65px;
      height: 65px;
    }
  }

  // .block-genplan__descr

  &__descr {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  // .block-genplan__bottom

  &__bottom {
    position: absolute;
    bottom: 25px;
    left: 25px;
    width: calc(100% - 50px);
    gap: 30px;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    pointer-events: none;
    z-index: 20;
    p {
      font-family: var(--second-family);
      font-weight: 800;
      font-size: 18px;
      line-height: 123%;
      letter-spacing: 0.03em;
      text-transform: uppercase;
      color: #fff;
      span {
        font-weight: 500;
      }
      @media (max-width: 992px) {
        display: none;
      }
    }
    @media (max-width: 768px) {
      left: 15px;
      width: calc(100% - 30px);
      gap: 20px;
    }
  }

  // .block-genplan__filter

  &__filter {
    flex: 0 0 auto;
    pointer-events: auto;
    @media (max-width: 768px) {
      margin: 0px 0px 2px 0px;
    }
  }
}

// Убираем старые классы скрытия
.block-genplan__content-container.step2,
.title-house {
  display: block; // Или flex, в зависимости от исходного значения
}

// Активные состояния
.block-genplan__content-container._active,
.block-genplan__title._active {
  opacity: 1;
  visibility: visible;
}

//Зум
