    display: none;
  }
  // .zoom-genplan__up

  &__up {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    border-radius: 11px;
    width: 37px;
    height: 37px;
    overflow: hidden;
    border: 3px solid rgba(0, 0, 0, 0.65);
    transition: opacity 0.3s;
    &:hover {
      opacity: 0.8;
    }
