      position: relative;
      z-index: 10;
      fill: #151515;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      background-color: #fff;
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      backdrop-filter: blur(12.445961952209473px);
    }
  }

  // .zoom-genplan__down

  &__down {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    border-radius: 11px;
    width: 37px;
    height: 37px;
    overflow: hidden;
    transition: opacity 0.3s;
    &:hover {
      opacity: 0.8;
    }
    svg {
      position: relative;
      z-index: 10;
      fill: #fff;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.65);
      z-index: 2;
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      backdrop-filter: blur(12.445961952209473px);
    }
  }
}

//Фуллскрин
.fullsreen-genplan {
