    position: relative;
    z-index: 10;
    font-weight: 400;
    font-size: 16px;
    letter-spacing: -0.03em;
    color: #fff;
  }

  // .fullsreen-genplan__icon

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 11px;
    overflow: hidden;
    width: 37px;
    height: 37px;
    position: relative;
    svg {
      position: relative;
      z-index: 10;
      fill: #fff;
    }
    &::before {
      content: "";
      position: absolute;
      top: 6px;
      left: 6px;
      width: calc(100% - 12px);
      height: calc(100% - 12px);
      border-radius: 3px;
      border: 2px solid #fff;
      z-index: 2;
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
