      height: 100%;
      backdrop-filter: blur(12.445961952209473px);
      background: rgba(0, 0, 0, 0.65);
    }
  }
}
.fullsreen-genplan-hint {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.65);
  transition: opacity 0.3s;
  z-index: 70;
  opacity: 0;
  pointer-events: none;
  &.fullscreen-hint_active {
    opacity: 1;
    pointer-events: auto;
  }
  p {
    font-weight: 400;
    font-size: 14px;
    letter-spacing: -0.03em;
    text-align: center;
    color: rgba(255, 255, 255, 0.75);
  }
  // .fullsreen-genplan-hint__icon

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 3px solid rgba(255, 255, 255, 0.4);
    border-radius: 50%;
    width: 60px;
    height: 60px;
    img {
      width: 31px;
      height: 31px;
      animation: moveArrow 1.5s ease-in-out infinite alternate;
    }
  }
}
@keyframes moveArrow {
  0% {
    transform: translateX(-5px);
  }
  100% {
    transform: translateX(5px);
  }
}

.panorama {
  width: 37px;
  height: 37px;
  display: flex;
