    display: flex;
  }
  img {
    width: 24px;
    height: 24px;
  }
  &:hover {
    opacity: 0.7;
  }
}

.block-genplan-popup {
  position: absolute;
  margin: 15px;
  color: #fff;
  z-index: 50;
  width: 350px;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s;
  &._active {
    opacity: 1;
    pointer-events: auto;
  }
  .choice-home & {
    width: 245px;
    @media (max-width: 768px) {
      top: auto !important;
      bottom: 20px !important;
      transform: translate(-50%, 0px);
      width: 100%;
      margin: 0px 0px 0px 0px;
      padding: 15px;
      .block-genplan-popup__close {
        right: 15px;
      }
    }
  }
  @media (max-width: 768px) {
    z-index: 60;
    top: 52% !important;
    left: 50% !important;
    bottom: auto !important;
    right: auto !important;
    transform: translate(-50%, -50%);
  }
  @media (max-width: 480px) {
    width: 100%;
    margin: 0px 0px 0px 0px;
    padding: 15px;
  }
  // .block-genplan-popup__content

  &__content {
    position: relative;
    padding: 15px;
    max-width: 350px;
    border-radius: 20px;
    margin: 0 auto;
    overflow: hidden;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      backdrop-filter: blur(16.94769287109375px);
      z-index: 2;
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.6);
    }
    @media (max-width: 768px) {
      max-width: 100%;
    }
    .choice-home & {
      max-width: 245px;
      @media (max-width: 768px) {
        max-width: 100%;
      }
    }
  }

  // .block-genplan-popup__close

  &__close {
    display: none;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 30px;
    height: 30px;
    background-color: #fff;
    border-radius: 50%;
    right: 0;
    top: -35px;
    transition: opacity 0.3s;
    svg {
      fill: #000;
    }
    &:hover {
      opacity: 0.7;
    }
    @media (max-width: 768px) {
      display: flex;
    }
    @media (max-width: 480px) {
      right: 15px;
      top: -25px;
    }
  }

  // .block-genplan-popup__title

  &__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 78%;
    letter-spacing: -0.03em;
    color: #fff;
    display: none;
    @media (max-width: 992px) {
      display: flex;
      order: 1;
    }
  }

  // .block-genplan-popup__body

  &__body {
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  // .block-genplan-popup__top

  &__top {
    position: relative;
    z-index: 10;
    border-radius: 10px;
    overflow: hidden;
    padding: 10px;
    @media (max-width: 992px) {
      order: 3;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      backdrop-filter: blur(10.541626930236816px);
      z-index: 2;
      border-radius: 10px;
    }
    &::after {
      content: "";
      border-radius: 10px;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.4);
    }
    ul {
      position: relative;
      z-index: 10;
      display: flex;
      flex-direction: column;
      gap: 10px;
      li {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
      }
    }
  }

  // .block-genplan-popup__name

  &__name {
    font-weight: 400;
    font-size: 14px;
    letter-spacing: -0.03em;
    color: rgba(255, 255, 255, 0.65);
    flex: 0 0 45%;
  }

  // .block-genplan-popup__value

  &__value {
    flex: 1 1 auto;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: -0.03em;
    text-align: right;
    color: #fff;
  }

  // .block-genplan-popup__image

  &__image {
    display: flex;
    flex-direction: column;
    gap: 10px;
    @media (max-width: 992px) {
      order: 2;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 5px;
    }
    p {
      font-weight: 400;
      font-size: 14px;
      line-height: 100%;
      color: #fff;
    }
  }

  // .block-genplan-popup__rooms

  &__rooms {
    @media (max-width: 992px) {
      order: 4;
    }
    ul {
      position: relative;
      z-index: 10;
      display: flex;
      flex-direction: column;
      gap: 10px;
      li {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
      }
    }
  }

  // .block-genplan-popup__room

  &__room {
    flex: 0 0 auto;
    background: rgba(217, 217, 217, 0.23);
    border-radius: 14px;
    padding: 8px 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 78%;
    letter-spacing: -0.03em;
    color: #fff;
  }

  // .block-genplan-popup__price

  &__price {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 90%;
    color: #fff;
  }

  // .block-genplan-popup__buttons

  &__buttons {
    display: flex;
    flex-direction: column;
    gap: 10px;
    @media (max-width: 992px) {
      order: 5;
    }
  }

  // .block-genplan-popup__button

  &__button {
    width: 100%;
  }
}

